import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FormInput from "../../../../Ui/Input/FormInput";
import FormCalendarInput from "../../../../Ui/Input/FormCalendarInput";
import {
  slashDateFormat,
  yearMonthDay,
} from "../../../../Utils/Functions/dateUtility";
import PaymentModeType from "../../../PaymentModeType/PaymentModeType";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { useParams } from "react-router-dom";
import { useStudentInstituteCharges } from "../../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import PaymentGatewayConfig from "../../../../../PaymentGateway/PaymentGatewayConfig";
import { get_initiate_paytmV2 } from "../../../../../Decrypt/paytmUpiInitiative";
import NotifyEvent from "../../../../Utils/Snackbar/NotifyEvent";
import dayjs from "dayjs";
const FinanceOtherFeesCollectWhenAdd = ({
  onClose,
  onParentClose,
  applicant,
  financeId,
  isOtherFees,
  onGrabCollectFees,
  singleStudent,
  isNonExisting,
  instituteId,
  ofid,
}) => {
  const { t } = useTranslation();
  const params = useParams();
  const [state, setState] = useState({
    amount: "",
    mode: "",
    fee_payment_mode: "",
    fee_payment_amount: "",
    transaction_date: dayjs().format("DD/MM/YYYY"),
    fee_bank_name: "",
    fee_bank_holder: "",
    fee_utr_reference: "",
  });
  const [errorField, setErrorField] = useState("");
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  // console.info("ambf sdgwd", applicant);
  // for payment gateway
  const [pgstate, setPgstate] = useState({
    amount: 0,
    type: "Other",
    isTransaction: false,
  });

  const { studentInstituteCharges } = useStudentInstituteCharges({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });

  useEffect(() => {
    if (isOtherFees === "OTHER_FEES") {
      setState((prev) => ({
        ...prev,
        amount: +applicant?.head,
        fee_payment_amount: +applicant?.head,
      }));
      setPgstate({
        amount: +applicant?.head ?? 0,
        type: "Other",
        isTransaction: false,
      });
    } else if (isOtherFees === "SUBJECT_OTHER_FEES") {
      if (applicant?.struct?.applicable_fees) {
        setState((prev) => ({
          ...prev,
          amount: +applicant?.struct?.applicable_fees,
          fee_payment_amount: +applicant?.struct?.applicable_fees,
        }));
        setPgstate({
          amount: +applicant?.struct?.applicable_fees ?? 0,
          type: "Other",
          isTransaction: false,
        });
      }
    } else {
    }
  }, [isOtherFees, applicant?.head, applicant?.struct?.applicable_fees]);

  const onInputAmount = (e) => {
    setState((pre) => ({
      ...pre,
      amount: +e.target.value,
      fee_payment_amount: +e.target.value,
    }));
    setPgstate((prev) => ({
      ...prev,
      amount: +e.target.value,
    }));
  };

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTransactionDate = (val) => {
    setState((prev) => ({
      ...prev,
      transaction_date: slashDateFormat(val),
    }));
  };

  const onModeChange = (val) => {
    setState((prev) => ({
      ...prev,
      mode: "Online",
      fee_payment_mode: val,
    }));
  };

  const onCollectFees = () => {
    let validation = requiredField(state, [
      "mode",
      "fee_payment_amount",
      "fee_bank_name",
      "fee_bank_holder",
      "fee_utr_reference",
    ]);
    if (existingRequiredField(validation)) {
      if (isOtherFees === "OTHER_FEES") {
        // if (state.amount > applicant?.head) {
        //   setErrorField({
        //     rest_issue: "Amount should not be greater than fees head fees.",
        //   });
        // } else {
        onGrabCollectFees({
          amount: state?.amount ?? 0,
          mode:
            state?.fee_payment_mode === "Cash" ? "Offline" : state?.mode ?? "",
          fee_payment_mode:
            state?.fee_payment_mode === "Cash"
              ? "By Cash"
              : state?.fee_payment_mode,
          fee_payment_amount: state?.fee_payment_amount ?? 0,
          transaction_date: yearMonthDay(state.transaction_date) ?? "",
          fee_bank_name: state?.fee_bank_name ?? "",
          fee_bank_holder: state?.fee_bank_holder ?? "",
          fee_utr_reference: state?.fee_utr_reference ?? "",
        });
        onParentClose();
        // }
      } else {
        if (state.amount > applicant?.struct?.applicable_fees) {
          setErrorField({
            rest_issue: "Amount should not be greater than applicable fees.",
          });
        } else {
          onGrabCollectFees({
            amount: state?.amount ?? 0,
            mode:
              state?.fee_payment_mode === "Cash"
                ? "Offline"
                : state?.mode ?? "",
            fee_payment_mode:
              state?.fee_payment_mode === "Cash"
                ? "By Cash"
                : state?.fee_payment_mode,
            fee_payment_amount: state?.fee_payment_amount ?? 0,
            transaction_date: yearMonthDay(state.transaction_date) ?? "",
            fee_bank_name: state?.fee_bank_name ?? "",
            fee_bank_holder: state?.fee_bank_holder ?? "",
            fee_utr_reference: state?.fee_utr_reference ?? "",
          });
          onParentClose();
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  const onCollectGatewayFees = () => {
    let validation = requiredField({
      amount: pgstate.amount,
    });
    if (existingRequiredField(validation)) {
      if (studentInstituteCharges?.institute) {
        if (financeId && singleStudent?._id && ofid) {
          setPgstate((prev) => ({
            ...prev,
            isTransaction: true,
          }));
        }
      } else {
        setSnackbar({
          open: true,
          msg: "Online Payment Not Activated",
          variant: "error",
        });
      }
      // if (pgstate.pay_remain && pgstate.amount) {
      //   setErrorField({
      //     rest_issue: "Amount should not be less than total remaining fees",
      //   });
      // } else {
      //   if (
      //     !pgstate.pay_remain &&
      //     pgstate.amount >
      //       applicant?.app_card?.remaining_array?.[0]?.remainAmount &&
      //     +applicant?.fee_struct?.total_installments > 1
      //   ) {
      //     setErrorField({
      //       rest_issue: "Amount should not be greater than remaining fees",
      //     });
      //   } else {
      //     if (studentInstituteCharges?.institute) {
      //       if (appId && financeId) {
      //         setPgstate((prev) => ({
      //           ...prev,
      //           isTransaction: true,
      //         }));
      //       }
      //     } else {
      //       setSnackbar({
      //         open: true,
      //         msg: "Online Payment Not Activated",
      //         variant: "error",
      //       });
      //     }
      //   }
      // }
    } else {
      setErrorField(validation);
    }
  };

  const onPgCheckoutHandler = () => {
    console.info("this is trigger");
    let gatewayCharges = (+pgstate?.amount * 2.1) / 100;
    let gst = (+gatewayCharges * 18) / 100;
    let withGst = gatewayCharges + gst;
    const data = parseInt(pgstate?.amount);
    var paymentData = {
      amount: Math.ceil(data + +withGst.toFixed(2)),
    };
    get_initiate_paytmV2({
      amount: +state?.amount ?? 10,
      amount_nocharges: +paymentData.amount ?? 0,
      type: "Other",
      paidBy: singleStudent?._id,
      paidTo: instituteId,
      moduleId: ofid,
      ad_status_id: null,
      payment_installment: null,
      ad_install: null,
      month: null,
      payment_remain_1: null,
      payment_card_type: null,
      payment_book_id: null,
      name: params.username,
      isApk: "web",
      payment_card_id: null,
      payment_remain_fees: null,
      charge: {
        num_platform_percent:
          studentInstituteCharges?.platform_charges?.percentage ?? 0,
        num_platform_max:
          studentInstituteCharges?.platform_charges?.maximum ?? 0,
        num_trans_pecent:
          studentInstituteCharges?.transaction_charges?.percentage ?? 0,
        num_trans_max:
          studentInstituteCharges?.transaction_charges?.maximum ?? 0,
        num_app_max: studentInstituteCharges?.application_charges ?? 0,
      },
      nsid: null,
      // url:`https://dashboard.qviple.com/q/${params.username}/feed?redirect=-1`
    })
      .then((data) => {
        window.onScriptLoad(
          data?.response?.body?.txnToken,
          data?.order,
          data?.amount
        );
      })
      .catch((e) => {
        console.info(e);
      });
  };

  // console.info("asdavsnm", pgstate);
  return (
    <>
      {pgstate.isTransaction ? (
        <PaymentGatewayConfig
          onClose={onClose}
          allContent={{
            ...pgstate,
            platform_charges:
              studentInstituteCharges?.platform_charges?.percentage,
          }}
          checkoutAdmissionHandler={onPgCheckoutHandler}
          isOptimzed
        />
      ) : (
        <Modal>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {t("collect_fees")}
                    {isNonExisting ? (
                      <span
                        style={{
                          fontWeight: "600",
                        }}
                      >
                        {" "}
                        {" - "}
                        {singleStudent?.student_name}
                      </span>
                    ) : (
                      <span
                        style={{
                          fontWeight: "600",
                        }}
                      >
                        {" "}
                        {" - "}
                        {`${singleStudent?.studentFirstName ?? ""} ${
                          singleStudent?.studentMiddleName ?? ""
                        } ${singleStudent?.studentLastName ?? ""}`}
                      </span>
                    )}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              {isOtherFees === "OTHER_FEES" ? (
                <div className={style.admission_fees_row}>
                  <h6>{t("applicable_amount")} :</h6>
                  <h6>
                    {t("rs_only")} {applicant?.head ?? 0}
                  </h6>
                </div>
              ) : (
                <div className={style.admission_fees_row}>
                  <h6>{t("applicable_amount")} :</h6>
                  <h6>
                    {t("rs_only")} {applicant?.struct?.applicable_fees ?? 0}
                  </h6>
                </div>
              )}

              <FormInput
                label={t("amount")}
                placeholder={t("amoute_details_collect")}
                name={"amount"}
                value={state.amount}
                type={"tel"}
                onChange={onInputAmount}
                errorShow={errorField["amount"]}
                msg={errorField["rest_issue"]}
              />
              <PaymentModeType
                paymentHeading={t("payment_type_mode")}
                selectLabel={t("mode_placeholder")}
                onClick={onModeChange}
                errorShow={errorField["fee_payment_mode"]}
                defalutValue={state.fee_payment_mode}
                selectedValue={t("please_select_payment_mode")}
                // isAuthority="admissionCollectModePg"
                isAuthority={
                  isNonExisting
                    ? "admissionCollectMode"
                    : "admissionCollectModePg"
                }
                customLabelStyle={{
                  marginTop: "0",
                }}
              />

              {state.fee_payment_mode === "Payment Gateway" ? (
                <CreateButton label="collect" onAction={onCollectGatewayFees} />
              ) : (
                <>
                  <FormCalendarInput
                    label={t("select_date_of_transaction")}
                    placeholder={t("please_select_date")}
                    name={"transaction_date"}
                    value={state.transaction_date}
                    onChange={onTransactionDate}
                    errorShow={errorField["transaction_date"]}
                  />
                  {state.fee_payment_mode === "Cash" ? null : (
                    <>
                      <FormInput
                        label={t("bank_name_collect")}
                        placeholder={t("please_enter_bank_name")}
                        name={"fee_bank_name"}
                        value={state.fee_bank_name}
                        type={"text"}
                        onChange={onInputChange}
                      />
                      <FormInput
                        label={t("banke_holder_name")}
                        placeholder={t("please_enter_name_of_bank_holder")}
                        name={"fee_bank_holder"}
                        value={state.fee_bank_holder}
                        type={"text"}
                        onChange={onInputChange}
                      />
                      <FormInput
                        label={t("cheque_dd_neft")}
                        placeholder={t("please_enter_utr_ref_number")}
                        name={"fee_utr_reference"}
                        value={state.fee_utr_reference}
                        type={"text"}
                        onChange={onInputChange}
                      />
                    </>
                  )}
                  <CreateButton label="collect" onAction={onCollectFees} />
                </>
              )}
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}

      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default FinanceOtherFeesCollectWhenAdd;
