import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FormInput from "../../../../Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
const OtherFeesAddNonExistingStudent = ({ onClose, onNext, singleStudent }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    student_name: "",
    classes: "",
    batch: "",
    roll_no: "",
  });
  const [errorField, setErrorField] = useState("");

  useEffect(() => {
    if (singleStudent) {
      setState((prev) => ({
        ...prev,
        student_name: singleStudent?.student_name ?? "",
        classes: singleStudent?.classes ?? "",
        batch: singleStudent?.batch ?? "",
        roll_no: singleStudent?.roll_no ?? "",
      }));
    }
  }, [singleStudent]);
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onNextValidate = () => {
    let validation = requiredField(state, ["classes", "batch", "roll_no"]);
    if (existingRequiredField(validation)) {
      onNext(state);
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_non_existing_student")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("student_name_only")}
              placeholder={t("student_name_placeholder")}
              name={"student_name"}
              value={state.student_name}
              type={"text"}
              onChange={onInputChange}
              errorShow={errorField["student_name"]}
            />
            <FormInput
              label={t("student_class")}
              placeholder={t("student_class_placeholder")}
              name={"classes"}
              value={state.classes}
              type={"text"}
              onChange={onInputChange}
            />
            <FormInput
              label={t("batch")}
              placeholder={t("batch_placeholder")}
              name={"batch"}
              value={state.batch}
              type={"text"}
              onChange={onInputChange}
            />
            <FormInput
              label={t("roll_number")}
              placeholder={t("roll_number_placeholder")}
              name={"roll_no"}
              value={state.roll_no}
              type={"text"}
              onChange={onInputChange}
            />

            <CreateButton label="next" onAction={onNextValidate} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default OtherFeesAddNonExistingStudent;
